<template>
<div class="boxx">
  <div id="map">

  </div>
</div>
</template>
<script>
export default {
  mounted() {
    // API
    var map = new BMap.Map("map"); // Map
    var point = new BMap.Point(116, 40);
    map.centerAndZoom(point, 11); // ,
    map.addControl(new BMap.MapTypeControl()); //
    map.setCurrentCity(""); //  
    map.enableScrollWheelZoom(true); //
  }
}
</script>
<style lang="scss" scoped>
.boxx {

 
}
#map {
  width:720px;
 height:450px;
 position: absolute;
  top:130px;
  
}
</style>



