<template>
  <div id="wzdinwei" v-show="!show">
    <div class="box">
    <div class="zb">
      <span class="cl"><b></b></span>
     <a href="#"><span class="el-icon-close" @click="show=!show"></span></a> 
    </div>
    <div class="boq">
      <div class="yi">
      	<!---->
        <!--<img src="../../assets/tu.png" alt="">-->
        <span>
          <b><p></p></b>
          <p>:B:110z</p>
          <p>:138xxxx7552</p>
        </span>
      </div>
    <div class="chel">
      <p>:</p>
      <p>::26:12</p>
      <p>:622827xxxxxxxxxx2915</p>
    </div>
   <div style="width:660px;height:320px;border:#ccc solid 1px;" id="dituContent"></div>


    </div>
    </div>
  </div>

</template>

<script>
   export default {
     data () {
    return {
        show:true,
      msg: 'Welcome to Your Vue.js App'
      }
   },
      mounted(){
        function initMap(){
        createMap();//
        setMapEvent();//
        addMapControl();//
    }
    
    //
    function createMap(){
        var map = new BMap.Map("dituContent");//
        var point = new BMap.Point(116.170937,40.182534);//
        map.centerAndZoom(point,17);//
        window.map = map;//map
    }
    
    //
    function setMapEvent(){
        map.enableDragging();//()
        map.enableScrollWheelZoom();//
        map.enableDoubleClickZoom();//()
        map.enableKeyboard();//
    }
    
    //
    function addMapControl(){
        //
	var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
	map.addControl(ctrl_nav);
        //
	var ctrl_ove = new BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
	map.addControl(ctrl_ove);
        //
	var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
	map.addControl(ctrl_sca);
    }
    
    
    initMap();//
      }
   }
</script>

<style lang='scss' scoped>
*{
  margin:0;
  padding: 0;
}
.iw_poi_title {color:#CC5522;font-size:14px;font-weight:bold;overflow:hidden;padding-right:13px;white-space:nowrap}
    .iw_poi_content {font:12px arial,sans-serif;overflow:visible;padding-top:4px;white-space:-moz-pre-wrap;word-wrap:break-word}
  #wzdinwei{
  width:100%;
  height:1080px; 
  background:rgba(0, 0, 0,0.5);
  padding-top:190px;
  .box{
    width:704px;
    height:690px;
    background:#fff; 
    margin: 0 auto;
    .zb{
      width:100%;
      height:48px;
      border-bottom:1px solid #aaaaaa;
      line-height:48px;
      .cl{
        float:left;
        margin-left:15px;
      }
      .el-icon-close{
        line-height:48px;
        margin-right:15px;
        float:right;
        font-size:12px;
      }
    }
    .boq{
      width:660px;
      margin:0 auto;
    }
    .yi{
      height:100px;
      border-bottom:2px solid #aaaaaa;
      padding: 20px;
      img{
        float:left;
        width: 120px;
        height:80px;
      }
      span{
        float:left;
        margin-left:20px;
        
      }
      
    }
    .chel{
      float:left;
      margin-top:28px;
      margin-bottom:28px;
    }
   
  }
    &:after {
    display: block;
    clear: both;
    content: "";
}

  }


</style>



