<template>
	<div class="">
		<el-button type="success" size="small"></el-button>
		<el-dialog title="" :visible.sync="dialogTableVisible">
		  <el-table :data="gridData">
		    <el-table-column property="date"></el-table-column>
		    <el-table-column property="name"></el-table-column>
		    <el-table-column property="address" class="hua">
		    </el-table-column>
		  </el-table>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogTableVisible = false"> </el-button>
		    <el-button type="primary" @click="dialogTableVisible = false"> </el-button>
		  </div>
		</el-dialog>
    </div>
</template>
<script>
export default {
    data() {
      return {
        gridData: [{
          date: '',
          name: '',
          address: ':1200000.00'
        }, {
          date: '',
          name: '',
          address: ':1200.00'
        },{
          date: '',
          name: '1200',
        },{
          date: '',
          name: '',
          address:''
        }
        ],
        dialogTableVisible: false,
        dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px'
      };
    }
};
</script>
<style lang="scss" scoped>
	.hua{
		color: red;
	}
</style>



